.videos {
  ul {
    background: #fff;
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
  li {
    border-bottom: 1px solid rgba(0, 0, 0, .15);
    &:last-child { border: 0; }
  }
  .video {
    align-items: center;
    display: flex;
    padding: .75rem 1rem;
    text-decoration: none;
  }
  .thumbnail {
    flex-basis: 96px;
    position: relative;
    img {
      display: block;
      width: 100%;
    }
    &::after {
      bottom: 0;
      box-shadow: inset rgba(0, 0, 0, .15) 0 0 0 1px;
      content: '';
      display: block;
      left: 0;
      position: absolute;
      right: 0;
      top: 0;
    }
  }
  aside {
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: center;
    min-width: 0;
    padding: 0 1rem;
  }
  h4, h5 { @include ellipsis; }
  .icon {
    height: 18px;
    opacity: .2;
    width: 18px;
  }

  @include breakpoint($breakpoint-tablet) {
    ul {
      background: none;
      display: grid;
      grid-gap: 1.5rem;
      grid-template-columns: repeat(auto-fill, minmax(20rem, 1fr));
      margin: 3rem 0;
    }
    li {
      border: 0;
      position: relative;
    }
    .video {
      display: block;
      padding: 0;
      position: relative;
      .thumbnail {
        box-shadow: 0px 1px 2px -1px rgba(0, 0, 0, 0.4),
                    0px 1px 2px 0px rgba(0, 0, 0, 0.3),
                    0px 1px 3px 0px rgba(0, 0, 0, 0.2);
        &::after {
          bottom: 0;
          box-shadow: inset 0 0 0 1px rgba(255, 255, 255, .15);
          content: '';
          display: block;
          left: 0;
          mix-blend-mode: overlay;
          position: absolute;
          right: 0;
          top: 0;
          @include z-index(front);
        }
      }
      aside {
        padding: .5rem;
        text-align: center;
      }
      .icon { @extend %hidden-tablet; }
    }
  }

  @include breakpoint($breakpoint-desktop) {
    ul {
      grid-gap: 2rem;
      grid-template-columns: repeat(auto-fill, minmax(24rem, 1fr));
    }
    .video {
      aside {
        bottom: 0;
        justify-content: flex-end;
        left: 0;
        opacity: 0;
        padding: 1.5rem;
        position: absolute;
        right: 0;
        top: 0;
        transition: opacity .2s;
        &::before {
          background-image: linear-gradient(to bottom, fade_out($purple, .2), fade_out($dark-purple, .1) 90%);
          bottom: 0;
          content: '';
          display: block;
          left: 0;
          position: absolute;
          right: 0;
          top: 0;
          @include z-index(default);
        }
        h4, h5 { @include z-index(front); }
      }
      .icon {
        @extend %shown-desktop;
        height: 60px;
        left: 50%;
        opacity: 0;
        position: absolute;
        top: 50%;
        transform: translateX(-50%) translateY(-50%);
        transition: opacity .2s;
        width: 60px;
        @include z-index(front);
      }
      &:hover {
        aside, .icon { opacity: 1; }
      }
    }
  }

  @include breakpoint($breakpoint-widescreen) {
    ul {
      grid-template-columns: repeat(auto-fill, minmax(26rem, 1fr));
    }
  }
}
